
body {
  font-family: Arial, sans-serif;
}

.container {
  max-width: 400px;
  margin: 50px auto;
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

h1 {
  text-align: center;
  color: #44475b;
  border-radius: 5px;

}
p {
  margin-top: 20px;
}

label {
  display: block;
  margin: 10px 10px;
  color: #44475b;
}

input {
  width: 25%;
  padding: 8px 4px;
  margin: 4px 100px;
  box-sizing: border-box;
  border-radius: 5px;
  border-color: whitesmoke;
  display: flex;
}

.header-container {
  margin: 32px 12px;
  border-radius: 20px;
  font-family: none;
  font-size: 15px;
  padding: 1px;
  background-color: #f6f6f9;
}

.calculator-container {
  margin: 32px 12px;
  border-radius: 20px;
  font-family: none;
  font-size: 15px;
  padding: 1px;
  background-color: #f6f6f9;
}

/* .calculator-container:hover {
  background-color: #e0e0e0;
  cursor: pointer;
} */

.result-container {
  margin: 32px 12px;
  border-radius: 20px;
  font-family: none;
  font-size: 15px;
  padding: 1px;
  background-color: #f6f6f9;
}
.result-Value {
  display: flex;
}

strong {
  color: #817f7f;
  padding: 10px 10px 10px 10px;
  margin: 10px -5px 5px 15px;
  font-family: none;
}

